import React from 'react';
import { Routes, Route, useLocation } from "react-router-dom";
import NavBar from "../Navbar/Navbar";
import Footer from "../Footer/Footer";
import Home from "../Pages/Home";
import AboutUsPage from "../Pages/AboutUsPage";
import ContactUs from "../Pages/ContactUs";
import KitchenPage from "../Pages/KitchenPage";
import BathroomPage from "../Pages/BathroomPage";
import ConstructionsPage from "../Pages/ConstructionsPage";
import SpecialConstructionsPage from "../Pages/SpecialConstructionsPage";
import FloorPage from "../Pages/FloorPage";
import LivingRoomPage from "../Pages/LivingRoomPage";
import LightingPage from "../Pages/LightingPage";
import HeatingPage from "../Pages/HeatingPage";
import LandingPage from "../Pages/LandingPage";

function PathAwareApp() {
    const location = useLocation();
    const isLandingPage = location.pathname === '/';

    return (
        <>
            {!isLandingPage && <NavBar />}
            <main className={location.pathname === '/' ? 'landing-page' : ''}>
                <Routes>
                    <Route path='/' element={<LandingPage />} />
                    <Route path='/home' element={<Home />} />
                    <Route path='/company' element={<AboutUsPage />} />
                    <Route path='/contact' element={<ContactUs />} />
                    <Route path='/services/kitchen' element={<KitchenPage />} />
                    <Route path='/services/bathroom' element={<BathroomPage />} />
                    <Route path='/services/special-constructions' element={<SpecialConstructionsPage />} />
                    <Route path='/services/floor' element={<FloorPage />} />
                    <Route path='/services/living-room' element={<LivingRoomPage />} />
                    <Route path='/services/lighting' element={<LightingPage />} />
                    <Route path='/services/heating' element={<HeatingPage />} />
                    <Route path='/projects/constructions' element={<ConstructionsPage />} />
                </Routes>
            </main>
            {!isLandingPage && <Footer />}
        </>
    );
}

export default PathAwareApp;
